@import "./variables";
@import "~roseworx/scss/roseworx";

@include snap-down-from('md') {
  .flex-rsp {
    display: flex;
    > div{
      width:50% !important;
      &:nth-child(even)
      {
        padding-left:$extra-small-spacing;
      }
      &:nth-child(odd)
      {
        padding-right:$extra-small-spacing;
      }
    }
  }
  h5 {
    font-size:24px;
    line-height: 36px;
  }
  h1 {
    font-size: 36px;
    line-height: 42px;
  }
  header {
    height: 50vh;
  }
}

@include snap-up-from('md') {
  .sp-mg {
    margin-bottom: 84px;      
  }
  header {
    height: 70vh;
  }
}

header {
  width:100%;
  position: relative;
  @include flex-center;
  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-width: 100%;
    min-height: 100%;
    z-index: -1;
    object-position: center;
    object-fit: cover;
  }
  .header-title {
    max-width: 700px;
  }
}

ul li{
  font-size: 12px;
  margin-bottom: 8px;
}

.navigation {
  // height:180px;
